<template>
    <el-form ref="form" :model="form" label-width="80px" size="medium" class="cl-form">
        <h1>数据抓取表</h1>
        <el-form-item label="数据ID">
            <el-input v-model="form.ids"></el-input>
        </el-form-item>
        <el-form-item label="网站来源">
            <el-select v-model="form.region" placeholder="请选择网站">
                <el-option label="溜溜网" value="LL"></el-option>
                <el-option label="建E网" value="JE"></el-option>
                <el-option label="欧模网" value="OM"></el-option>
                <el-option label="知末网" value="ZM"></el-option>
            </el-select>
        </el-form-item>

        <el-form-item>
            <el-button type="primary" @click="onSubmit">立即搜索</el-button>
            <el-button>取消</el-button>
        </el-form-item>

        <!--搜索结果表格-->
        <el-table :data="datas" border style="width: 100%">
            <el-table-column prop="id" label="ID" width="180"></el-table-column>
            <el-table-column prop="money" label="金币" width="180"></el-table-column>
            <el-table-column prop="name" label="名字"></el-table-column>
        </el-table>

    </el-form>
</template>
<style>
    .cl-form {
        margin: 5% auto;
        width: 60%;
    }

</style>
<script>
    import {getLLPrice, getJEPrice, getOMPrice, getZMPrice} from '../api/api.js';
    export default {
        data() {
            return {
                form: {
                    ids: '',
                    region: ''
                },
                url: '',
                datas: []
            }
        },
        methods: {
            onSubmit() {
                let para = {
                    ids: this.form.ids
                };
                if (this.form.region == 'LL') {
                    getLLPrice(para).then((res) => {
                        let list = res.data.data;
                        this.datas = list;
                    });
                } else if (this.form.region == 'JE') {
                    getJEPrice(para).then((res) => {
                        let list = res.data.data;
                        this.datas = list;
                    });
                } else if (this.form.region == 'OM') {
                    getOMPrice(para).then((res) => {
                        let list = res.data.data;
                        this.datas = list;
                    });
                } else if (this.form.region == 'ZM') {
                    getZMPrice(para).then((res) => {
                        let list = res.data.data;
                        this.datas = list;
                    });
                }
                console.log('submit!');
            }
        }
    }
</script>


